.w {
  position: absolute;
  z-index: 4;
  padding: 0.5em 1em;
  font-size: 11px;
  color: var(--t-fg-default);
  cursor: move;
  background-color: var(--t-bg-highlight);
  border-radius: 0.5em;
  transition: background-color 0.15s ease-out, color 0.15s ease-out, border-bottom-width 0.1s linear, border-top-width 0.1s linear;
}
.w ul {
  padding: 0;
  margin: 0;
}
.w.computer {
  border: 3px solid #CE192A;
}
.w.player {
  border: 3px solid #196CCE;
}
.w.situation {
  border: 3px solid #F37812;
}
.w.editing, .w.jtk-drag {
  z-index: 5;
}
.w .nodeContent {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  padding: 0.5em;
  background: transparent;
}
.w.ancestorOfSelected.computer, .w.descendantOfSelected.computer {
  color: #222;
  background-color: #fad3d7;
}
.w.ancestorOfSelected.player, .w.descendantOfSelected.player {
  color: #222;
  background-color: #d3e5fa;
}
.w.ancestorOfSelected.situation, .w.descendantOfSelected.situation {
  color: #222;
  background-color: #fef4ec;
}
.w.parentOfSelected.computer, .w.childOfSelected.computer {
  color: #222;
  background-color: #f4a5ad;
}
.w.parentOfSelected.player, .w.childOfSelected.player {
  color: #222;
  background-color: #a5c9f4;
}
.w.parentOfSelected.situation, .w.childOfSelected.situation {
  color: #222;
  background-color: #fcd9bc;
}
.w.ancestorOfSelected {
  border-bottom-width: 6px;
}
.w.parentOfSelected {
  border-bottom-width: 8px;
}
.w.descendantOfSelected {
  border-top-width: 6px;
}
.w.childOfSelected {
  border-top-width: 8px;
}
.w:hover.computer, .w.color-label.jtk-hover.computer, .w.jtk-source-hover.computer, .w.jtk-target-hover.computer {
  color: #222;
  background-color: #f4a5ad;
}
.w:hover.player, .w.color-label.jtk-hover.player, .w.jtk-source-hover.player, .w.jtk-target-hover.player {
  color: #222;
  background-color: #a5c9f4;
}
.w:hover.situation, .w.color-label.jtk-hover.situation, .w.jtk-source-hover.situation, .w.jtk-target-hover.situation {
  color: #222;
  background-color: #fcd9bc;
}
.w.ui-selecting.computer {
  color: #222;
  background-color: #f4a5ad;
}
.w.ui-selecting.player {
  color: #222;
  background-color: #a5c9f4;
}
.w.ui-selecting.situation {
  color: #222;
  background-color: #fcd9bc;
}
.w.selected, .w.ui-selected, .w.jtk-drag-selected {
  z-index: 5;
}
.w.selected.computer, .w.ui-selected.computer, .w.jtk-drag-selected.computer {
  color: white;
  background-color: #e94a59;
}
.w.selected.player, .w.ui-selected.player, .w.jtk-drag-selected.player {
  color: white;
  background-color: #4a93e9;
}
.w.selected.situation, .w.ui-selected.situation, .w.jtk-drag-selected.situation {
  color: #222;
  background-color: #f7a15b;
}
.w.selected.ui-selecting.computer, .w.ui-selected.ui-selecting.computer, .w.jtk-drag-selected.ui-selecting.computer {
  color: white;
  background-color: #e41d30;
}
.w.selected.ui-selecting.player, .w.ui-selected.ui-selecting.player, .w.jtk-drag-selected.ui-selecting.player {
  color: white;
  background-color: #1d78e4;
}
.w.selected.ui-selecting.situation, .w.ui-selected.ui-selecting.situation, .w.jtk-drag-selected.ui-selecting.situation {
  color: #222;
  background-color: #f4862a;
}
.w:not(.jtk-drag):not(.jtk-drag-active) > .ep {
  position: absolute;
  top: -10px;
  right: -10px;
  bottom: -10px;
  left: -10px;
  cursor: pointer;
  border-radius: 12px;
  transition: box-shadow 0.15s ease-in, color 0.15s ease-in;
}
.w:not(.jtk-drag):not(.jtk-drag-active) > .ep:hover {
  box-shadow: 0 0 15px 2px rgba(0, 0, 0, 0.15), 0 1px 3px 0 rgba(0, 0, 0, 0.15);
}
.w:not(.jtk-drag):not(.jtk-drag-active) > .ep:hover .anchor {
  background-color: black;
  transition: background-color 0.25s ease;
}
.w:not(.jtk-drag):not(.jtk-drag-active) > .ep:hover .anchor::after {
  opacity: 0.25;
  transform: scale(5);
}
.w:not(.jtk-drag):not(.jtk-drag-active) > .ep .anchor {
  position: absolute;
  top: 50%;
  right: auto;
  bottom: 50%;
  left: calc(100% - 10px);
  width: 4px;
  height: 4px;
  pointer-events: none;
  background-color: transparent;
  border-radius: 50%;
  transition: background-color 0.25s ease, left 0s step-end 0.15s, top 0s step-end 0.15s, right 0s step-end 0.15s, bottom 0s step-end 0.15s;
}
.w:not(.jtk-drag):not(.jtk-drag-active) > .ep .anchor::after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: block;
  background-color: black;
  border-radius: 50%;
  opacity: 0;
  transition: opacity 0.25s ease, transform 0.25s ease;
}
.w:not(.jtk-drag):not(.jtk-drag-active):not(.jtk-connected) > .ep > .anchor {
  background-color: rgba(0, 0, 0, 0.75);
  transition: background-color 0.25s ease, left 0.15s linear, top 0.15s linear, right 0.15s linear, bottom 0.15s linear;
}
.w:not(.jtk-drag):not(.jtk-drag-active):not(.jtk-connected) > .ep:hover > .anchor {
  background-color: black;
  transition: background-color 0.25s ease, left 0.15s linear, top 0.15s linear, right 0.15s linear, bottom 0.15s linear;
}
.w > .ep::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: 100%;
  height: 100%;
  border: 4px solid transparent;
  border-radius: 10px;
  box-sizing: border-box;
}
.w.highlight-value-positive > .ep::before {
  border-color: rgba(34, 170, 51, 0.35);
}
.w.highlight-value-neutral > .ep::before {
  border-color: rgba(243, 120, 18, 0.35);
}
.w.highlight-value-negative > .ep::before {
  border-color: rgba(206, 25, 42, 0.35);
}
.w.highlight-no-effect > .ep::before {
  border-color: rgba(121, 152, 190, 0.35);
}
.w.highlight-operator-addAssign > .ep::before, .w.highlight-operator-subtractAssign > .ep::before {
  border-style: dashed;
}
.w.highlight-operator-subtractAssign > .ep::before {
  border-color: rgba(206, 25, 42, 0.35);
}

.color-label.jtk-hover,
.jtk-source-hover,
.jtk-target-hover {
  color: white;
}

.color-label {
  padding: 0.3em;
  cursor: pointer;
  background-color: white;
  border: 1px solid rgba(0, 0, 0, 0.5);
  border-radius: 0.5em;
  opacity: 0.8;
  transition: background-color 0.15s ease-in, color 0.15s ease-in;
}

.jtk-endpoint {
  z-index: 3;
}

.jtk-connector path {
  cursor: pointer;
}